export default function() {
	let token = uni.getStorageSync("token");
	if (!token) {
		uni.showModal({
			title: '',
			content: '',
			success: function(res) {
				if (res.confirm) {
					uni.navigateTo({
						url: "/pages/user/pages/login/index"
					})
				}
			}
		});
	}
	return token ? true : false
}
